.login-main {
    width: 100%;
    min-height: 100vh;
}

.login-nav {
    padding: 20px 0px;
}

.login-container {
    max-width: 100%;
    overflow: hidden;
}

.login-sec {
    min-height: 75vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.login-sec img {
    max-width: 90%;
    max-height: 500px;
}

.login-box {
    width: 400px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.login-row button {
    width: 100%;
    height: 45px;
    border: none;
    outline: none;
    background-image: linear-gradient(to right, var(--mainColor), var(--secondColor));
    color: white;
    font-weight: 500;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
}

.login-row {
    display: flex;
    justify-content: center;
}

.login-row.head h3 {
    color: var(--mainColor);
}

.login-box .input div {
    border-radius: 10px;
    background-color: var(--lightBg);
    border-color: var(--lightGrey);
}

.login-box .input fieldset {
    border-color: var(--lightGrey);
}

.login-nav h3 {
    background: -webkit-linear-gradient(var(--secondColor), var(--mainColor));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 850;
}

@media(max-width:600px) {
    .login-sec {
        height: 100%;
    }
    .login-sec.sec-2{
        min-height: 50vh ;
    }
    .login-nav{
        padding: 20px;
        position: fixed;
        top: 0px;
    }
    .login-container{
        padding-top: 50px;
    }
    .login-box .input div {
        height:50x;
    }
}

.admin-header{
