:root {
  --mainColor: #5096F3;
  --secondColor: #1765F3;
  --lightBg: #fafafa;
  --lightGrey: #e2e2e2;
  --adminBg: #f9fafb;
  --adminMain: #7e3af2;
  --defaultShadow: 0px 0px 10px 10px rgba(128, 128, 128, 0.05);
  --btnBg: linear-gradient(to right, var(--mainColor), var(--secondColor));
}

* {
  padding: 0px;
  margin: 0px;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  background-color: var(--lightBg);
}


.App {
  max-width: 100vw;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

button:disabled {
