    object-fit: contain
}

@-webkit-keyframes startAnime {
    from{
        opacity: 0%;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    to{
        opacity: 100%;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes startAnime {
    from{
        opacity: 0%;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
    to{
        opacity: 100%;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

