.user-booking-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    padding-top: 20px;
}

.user-booking-item {
    background-color: #fff;
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    border-radius: 10px;
    box-shadow: var(--defaultShadow);
    padding: 10px;
}

.ub-dr-profile img {
    width: 90px;
    height: 120px;
    object-fit: cover;
    border-radius: 10px;
}

.ub-dr-desc {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
}

.ub-dr-desc-item {
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.ub-dr-desc-item div {
    display: flex;
    height: 27px;
    font-size: .9rem;
}

.ub-dr-desc-item div p:last-child {
    font-weight: 550;
    margin-left: 5px;
}

.ub-dr-desc {
    padding: 0px 10px;
}
@media(max-width:600px){
    .booking-status{
        width: 100px;
    }
    .ub-dr-desc{
        min-width: 260px;
    }
}
.report-box{
    width: 100%;
    min-height: 300px;
    background-color: #fff;
    margin-bottom: 20px;
    border-radius: 10px;
    box-shadow: var(--defaultShadow);
    display: flex;
    flex-direction: column;
    padding: 20px;
    grid-gap: 15px;
    gap: 15px;

}
.report-box.total{
    display: flex;
}
.report-box.total .report-box-item{
    flex: 50% 1;
    box-shadow: var(--defaultShadow);
    padding: 11px 20px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
}
.report-box-item .icon{
    font-size: 2.5rem;
}
.report-box-item div:last-child{
    display: flex;
    align-items: center;
}
.report-input{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
}

.report-input-radio{
    height: 40px;
    /* min-width: 100px; */
    width: 100%;
    display: flex;
    align-items: center;
    border-radius: 5px;
    border: 1px solid var(--mainColor);

}
.report-input-radio input{
    width: 5px;
}
.report-input-radio label{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.report-input-radio input:checked +label{
    background-color: var(--mainColor);
    color: white;
}

.report-input-row{
    display: flex;
    width: 100%;
    grid-gap: 10px;
    gap: 10px;

}
.report-input-date-picker{
    width: 100%;
}

